.hero {
  position: relative;
  min-height: s(1018);
  &::before,
  &::after {
    position: absolute;
    content: '';
  }
  &::before {
    top: s(-180);
    left: s(135);
    @include block(s(226), s(1302));
    opacity: 0.55;
    filter: blur(s(150));
    background: linear-gradient(180deg, var(--blue) 0%, rgba(71, 139, 249, 0.4) 100%);
    transform: rotate(-54.311deg);
  }
  &::after {
    right: s(-90);
    bottom: s(-120);
    @include block(s(574));
    border-radius: 100%;
    opacity: 0.7;
    background: linear-gradient(225deg, var(--blue) 0%, #9dadf2 100%);
    filter: blur(s(325));
  }
  &__container {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    position: relative;
    z-index: 10;
    width: 100%;
    max-width: s(650);
    padding-top: s(296);
  }
  &__title,
  &__desc {
    font-weight: 500;
  }
  &__title,
  &__link {
    text-transform: uppercase;
  }
  &__title {
    display: flex;
    flex-direction: column;
    margin-bottom: s(25);
    & > span {
      display: inline-block;
      font-size: s(96);
      line-height: 100%;
      &:last-child {
        color: var(--blue);
      }
    }
  }
  &__desc {
    margin-bottom: s(48);
    font-size: s(32);
    line-height: 100%;
    color: rgba(255, 255, 255, 0.6);
  }
  &__link {
    text-align: center;
    border-radius: s(48);
    padding: s(20) s(72);
    font-size: s(24);
    font-weight: 600;
    line-height: s(32);
    background-color: var(--blue);
  }
  @include mobile {
    padding-bottom: s-md(120);
    &::before {
      top: s-md(-50);
      left: s-md(105);
      @include block(s-md(81), s-md(466));
      filter: blur(s-md(40));
    }
    &::after {
      bottom: 0;
      right: s-md(-80);
      @include block(s-md(240));
      filter: blur(s-md(135));
    }
    &__container {
      flex-direction: column;
      padding: 0;
    }
    &__content {
      display: flex;
      flex-direction: column;
      padding: s-md(60) s-md(10) s-md(32);
    }
    &__title,
    &__desc {
      text-align: center;
    }
    &__title {
      margin-bottom: s-md(14);
      & > span {
        font-size: s-md(46);
        line-height: s-md(50);
      }
    }
    &__desc {
      margin-bottom: s-md(22);
      font-size: s-md(20);
      line-height: s-md(26);
    }
    &__link {
      font-size: s-md(18);
      border-radius: s-md(48);
      padding: s-md(19) s-md(15);
    }
  }
}

.slider {
  position: absolute;
  overflow: hidden;
  z-index: -1;
  display: flex;
  top: 0;
  left: 50%;
  height: 100%;
  gap: s(17);
  transform: translateX(-32.5%);
  &::after {
    position: absolute;
    content: '';
    @include block(200%, 100%);
    top: 60%;
    left: 50%;
    z-index: 10;
    background: linear-gradient(0deg, var(--blue-dark) 63.64%, rgba(255, 255, 255, 0) 95.45%);
    transform: translateX(-50%);
  }
  &__item {
    &,
    &-wrapper {
      display: flex;
      flex-direction: column;
      gap: s(27);
    }
    &--left {
      opacity: 0.2;
    }
    &--center {
      opacity: 0.6;
    }
    &--right {
      opacity: 0.4;
    }
    &-wrapper {
      animation-timing-function: linear;
      animation-duration: 10s;
      animation-iteration-count: infinite;
    }
    &.left > &-wrapper {
      animation-name: infinite-slide-1;
      @keyframes infinite-slide-1 {
        from {
          transform: translateY(0);
        }
        to {
          transform: translateY(-50%);
        }
      }
    }
    &.right > &-wrapper {
      animation-name: infinite-slide-2;
      @keyframes infinite-slide-2 {
        from {
          transform: translateY(-50%);
        }
        to {
          transform: translateY(0);
        }
      }
    }
  }
  &__slide {
    @include block(s(100), auto);
    &.long {
      position: relative;
      height: s(200);
      &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        @include block(100%);
        border: s(4) solid currentColor;
        border-radius: s(100);
      }
      & > svg {
        @include block(inherit, auto);
      }
      &-end {
        display: flex;
        align-items: flex-end;
      }
    }
    & > svg {
      fill: currentColor;
      @include block(inherit);
    }
  }
  @include mobile {
    overflow: hidden;
    position: static;
    flex-direction: column;
    gap: s-md(9);
    transform: translate(0, 0);
    &::after {
      content: none;
    }
    &__item {
      &,
      &-wrapper {
        display: flex;
        flex-direction: row;
      }
      &--left {
        opacity: 0.4;
      }
      &--center {
        opacity: 0.5;
      }
      &--right {
        opacity: 0.1;
      }
      &.left > &-wrapper {
        @keyframes infinite-slide-1 {
          from {
            transform: translateX(0);
          }
          to {
            transform: translateX(-50%);
          }
        }
      }
      &.right > &-wrapper {
        @keyframes infinite-slide-2 {
          from {
            transform: translateX(-50%);
          }
          to {
            transform: translateX(0);
          }
        }
      }
    }
    &__slide {
      @include block(auto, s-md(49));
      &.long {
        @include block(s-md(108), s-md(49));
        &::after {
          border-radius: s-md(49);
        }
        & > svg {
          @include block(auto, s-md(49));
        }
        // &-end {
        //   display: flex;
        //   align-items: flex-end;
        // }
      }
    }
  }
}

.blog {
  position: relative;
  z-index: 10;
  padding-top: s(141);
  &__title {
    text-align: center;
    width: 100%;
    margin-bottom: s(24);
    font-size: s(40);
    font-weight: 600;
    line-height: 100%;
  }
  &__list {
    width: s(526);
    height: s(589);
    padding: s(22) s(38) s(24) s(21);
    &--box {
      overflow: hidden;
      border-radius: s(100);
      border: s(3) solid var(--blue);
      backdrop-filter: blur(s(10));
      background-color: rgba(255, 255, 255, 0.12);
    }
  }
  &__item {
    border-radius: s(196);
    padding: s(16) s(64);
    @include transition(background-color, var(--blue-dark));
    &:hover {
      background-color: var(--blue);
    }
    &:not(:last-child) {
      margin-bottom: s(16);
    }
  }
  &__link {
    display: flex;
    flex-direction: column;
    gap: s(14);
    &--title {
      font-size: s(24);
      font-weight: 400;
      line-height: s(28);
    }
    &--date {
      font-family: 'Futura', sans-serif;
      font-size: s(14);
      font-weight: 400;
      line-height: s(16);
      color: rgba(255, 255, 255, 0.6);
    }
  }
  @include mobile {
    padding: s-md(33) s-md(10) 0;
    &__title {
      margin-bottom: s-md(18);
      font-size: s-md(32);
      line-height: s-md(40);
    }
    &__list {
      width: 100%;
      height: s-md(417);
      padding: s-md(8) s-md(16) s-md(8) s-md(8);
      &--box {
        border-radius: s-md(50);
        border-width: s-md(2);
        backdrop-filter: blur(s-md(10));
      }
    }
    &__item {
      border-radius: s-md(40);
      padding: s-md(15) s-md(35);
      &:not(:last-child) {
        margin-bottom: s-md(9);
      }
    }
    &__link {
      gap: s-md(7);
      &--title {
        font-size: s-md(16);
        line-height: s-md(20);
      }
      &--date {
        font-size: s-md(14);
        line-height: s-md(16);
      }
    }
  }
}

.simplebar-scrollbar {
  cursor: pointer;
  &::before {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 1 !important;
    background: var(--blue);
  }
}

.simplebar-track {
  right: s(14);
  &.simplebar-vertical {
    border-radius: s(10);
    top: 50%;
    height: 70%;
    width: s(8);
    transform: translateY(-50%);
    background-color: rgba(255, 255, 255, 0.12);
  }
  @include mobile {
    right: s-md(5);
    &.simplebar-vertical {
      height: 72%;
    }
  }
}
