// Перерасчёт из px в rem на ширине экрана 1920px
@function s($px) {
  @return calc($px / 10) + rem;
}
// Перерасчёт из px в rem на ширине экрана 768px
@function s-md($px) {
  @return calc(($px / 10) * 2) + rem;
}

//                                                            Подключение mixins
@import 'mixin';
//                                                            Подключение основных переменных
@import 'static/variables';
//                                                            Подключение шрифтов
@import 'static/fonts';

// // Основной шрифт
$font-family: 'Lexend';

@include font('Futura', 'Futura', 400);

html,
body {
  user-select: none;
  margin: 0 auto;
  padding: 0;
  font-family: $font-family, sans-serif; // шрифт по умолчанию
  font-weight: 400; // жирность шрифта по умолчанию
  font-style: normal;
  line-height: 100%;
}

html {
  font-size: 0.5208335vw; // на ширине экрана 1920px (0.5208335vw === 10px)
  @include mobile {
    font-size: 5px;
    font-size: 1.5625vw;
    font-size: calc(100 / $breakpoint-mobile) * 5vw; // где $breakpoint-mobile - это ширина мобильной версии в px
    -webkit-text-size-adjust: none;
  }
}

body {
  overflow-x: hidden;
  font-size: s(16); // размер шрифта по умолчанию (16px)
  color: var(--white); // цвет текста по умолчанию
  background-color: var(--blue-dark); // заливка по умолчанию
  @include mobile {
    font-size: s-md(16); // размер шрифта по умолчанию (16px)
    -webkit-text-size-adjust: none;
  }
}

//                                                            Подключение сброса стандартных стилей
@import 'static/reset';
//                                                            Подключение кастомных стилей к элементам
// @import 'static/custom-element';
//                                                            Подключение стилей к Yandex картам
// @import 'static/yandex-map';

.container {
  width: s(1640);
  margin: 0 auto;
  @include mobile {
    width: 100%;
    padding: 0 s-md(10);
  }
}

.desktop {
  @include mobile {
    display: none;
  }
}

.mobile {
  display: none;
  @include mobile {
    display: initial;
  }
}

.us-link {
  position: relative;
  @include transition(color);
  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    @include block(100%, s(1));
    background-color: var(--blue);
    @include transition(opacity, 0);
  }
  &:hover,
  &:focus,
  &:active {
    color: var(--blue);
    &::after {
      opacity: 1;
    }
  }
}

@import './header';
@import './footer';
