.header {
  position: relative;
  padding: s(16) 0 s(8);
  &__container,
  &__list,
  &__lang + .ui-selectmenu-button {
    @include flex-50-y;
  }
  &__container {
    justify-content: space-between;
    border: s(4) solid var(--blue);
    border-radius: s(64);
    padding: s(20) s(43);
  }
  &__logo {
    width: s(287);
  }
  &__list {
    gap: s(13.5);
  }
  &__lang + .ui-selectmenu-button,
  &__auth,
  &__reg {
    padding: s(14) s(13);
    border-radius: s(24);
    text-transform: uppercase;
  }
  &__auth,
  &__lang + .ui-selectmenu-button {
    border: s(2) solid var(--purple);
  }
  &__lang + .ui-selectmenu-button {
    & * {
      font-family: $font-family, sans-serif;
      font-size: s(16);
      font-weight: 400;
      line-height: 100%;
    }
    gap: s(8);
    width: auto;
    margin: 0;
    background-color: transparent !important;
    color: var(--white) !important;
