62C3.55057 11.6711 4.26377 11.4508 4.99322 11.2277C5.49096 12.8312 6.2522 14.2183 7.35894 15.5Z' fill='%239DADF2'/%3E%3Cpath d='M10.0408 4.86013C9.60158 3.48479 8.92817 2.28503 7.99711 1.19646C7.06019 2.30258 6.38093 3.4965 5.95931 4.86013C7.33541 5.04156 8.67052 5.04741 10.0408 4.86013Z' fill='%239DADF2'/%3E%3Cpath d='M8.00297 14.8035C7.07191 13.7208 6.39849 12.521 5.95931 11.1457C7.3237 10.9526 8.65881 10.9584 10.0466 11.1399C9.60744 12.5093 8.93403 13.7033 8.00297 14.8035Z' fill='%239DADF2'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_103_272'%3E%3Crect width='16' height='16' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E") !important;
      background-position: center center;
      background-size: cover;
    }
    & > .ui-selectmenu-text {
      margin-right: 0;
    }
  }
  &__reg {
    background-color: var(--blue);
    border: s(2) solid var(--blue);
  }
  @include mobile {
    padding: s-md(10);
    &__container {
      border-width: s-md(2);
      border-radius: s-md(64);
      padding: s-md(8) s-md(13);
    }
    &__list {
      gap: s-md(4);
    }
    &__lang + .ui-selectmenu-button {
      justify-content: center;
      @include block(s-md(36));
      gap: 0;
      border-width: s-md(2);
      border-radius: 100%;
      & > .ui-selectmenu-icon {
        @include block(s-md(20));
      }
      & > .ui-selectmenu-text {
        visibility: hidden;
        opacity: 0;
        @include block(0);
        font-size: 0;
        line-height: 0;
      }
    }
  }
}

.navigate {
  &__list {
    @include flex-50-y;
    gap: s(32.5);
  }
  @include mobile {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    top: calc(100% - s-md(10));
    left: 0;
    z-index: 50;
    width: 100%;
    padding: s-md(30) s-md(10) s-md(92);
    background-color: var(--blue-dark);
    @include transitions((visibility, opacity));
    &.active {
      visibility: visible;
      opacity: 1;
    }
    &__toggle {
      @include flex-50-50;
      flex-direction: column;
      @include block(s-md(36));
      gap: s-md(3.5);
      & > span {
        display: block;
        opacity: 1;
        @include block(s-md(21.5), s-md(2));
        border-radius: s-md(2);
        background-color: var(--white);
        transform: translate(0, 0) rotate(0);
        @include transitions((transform, opacity));
      }
      &.active {
        & > span {
          &:first-child {
            transform: translateY(s-md(5.5)) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            transform: translateY(s-md(-5.5)) rotate(-45deg);
          }
        }
      }
    }
    &__list {
      align-items: initial;
      flex-direction: column;
      gap: s-md(4);
      padding-bottom: s-md(29);
    }
    &__link {
      width: 100%;
      border-radius: s-md(28);
      border: s-md(2) solid var(--blue);
      padding: s-md(12) s-md(18);
      font-size: s-md(18);
      line-height: s-md(24);
      color: var(--white) !important;
      &::after {
        content: none;
      }
    }
    &__auth,
    &__reg {
      text-align: center;
      border-width: s-md(2);
      border-style: solid;
      border-radius: s-md(24);
      padding: s-md(13) s-md(20);
    }
    &__auth {
      border-color: var(--purple);
      background-color: var(--purple-dark);
      &-box {
        display: flex;
        flex-direction: column;
        gap: s-md(6);
      }
    }
    &__reg {
      border-color: var(--blue);
      background-color: var(--blue);
    }
  }
}

.ui-menu {
  border: none;
  background-color: var(--purple);
  border-radius: s(2);
  & > .ui-menu-item {
    border: none;
    margin: 0 auto;
    & * {
      text-transform: uppercase;
      font-family: $font-family, sans-serif;
      font-size: s(16);
      font-weight: 400;
      line-height: 100%;
      color: var(--white);
    }
    & > .ui-menu-item-wrapper {
      text-align: center;
      border: s(1) solid var(--white);
      padding: s(2);
      background-color: var(--purple) !important;
    }
  }
  @include mobile {
    border-radius: s-md(2);
    & > .ui-menu-item {
      & * {
        font-size: s-md(16);
      }
      & > .ui-menu-item-wrapper {
        padding: s-md(6) s-md(2);
      }
    }
  }
}
